import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import outdoorTest from '@/views/outdoorTest/outdoorTest'
import test from '@/views/test/test'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/outdoorTest',
      name: 'outdoorTest',
      component: outdoorTest
    },
    {
      path: '/test',
      name: 'test',
      component: test
    }
  ]
})



