<template>
  <div id="playBack">
    <a-row>
      <a-col :span="16">
        <a-card size="small" title=" L3 MESSAGE1">
          <div slot="extra">
            <div style="padding: 3px;background: #fff">
              <Select v-model="eventType" style="width:100px" multiple>
                <OptionGroup label="正常事件">
                  <Option v-for="item in normalEvents" :value="item" :key="item">{{ item }}</Option>
                </OptionGroup>
                <OptionGroup label="异常事件">
                  <Option v-for="item in abNormalEvents" :value="item" :key="item">{{ item }}</Option>
                </OptionGroup>
              </Select>

              <a-button type="primary" size="small">查询</a-button>
              <a-icon :type="rollBackName" @click="rollBack" theme="twoTone"
                      style="font-size: 14px;vertical-align: middle;cursor: pointer"
              />
              <a-icon type="exclamation-circle" @click="resetRollBack" theme="twoTone"
                      style="font-size: 14px;vertical-align: middle;cursor: pointer"/>

              <a-progress :percent="percent" size="small"/>
              <a-select v-model="speed" style="width: 40px" size="small">
                <a-select-option :value="1">1x</a-select-option>
                <a-select-option :value="0.5">2x</a-select-option>
                <a-select-option :value="0.25">4x</a-select-option>
              </a-select>
              <!--<a-button type="primary">清除</a-button>-->
            </div>
          </div>
          <div id="map" style="width: 100%;height: 55vh">
            <div class="leaflet-bar leaflet-control leaflet-marker">
              <a-popover placement="right">
                <template slot="content">
                  <p>轨迹点大小：</p>
                  <a-radio-group name="radioGroup" v-model="pointSize" @change="changeMarkerSize">
                    <a-radio :value="2"><img src="../../../../static/img/point1.png"></a-radio>
                    <a-radio :value="4"><img src="../../../../static/img/point2.png"></a-radio>
                    <a-radio :value="6"><img src="../../../../static/img/point3.png"></a-radio>
                  </a-radio-group>
                  <p>是否显示RSRP/SINR：</p>
                  <a-radio-group name="radioKPI" v-model="kpiValue" @change="changeKpiValue">
                    <a-radio value="RSRP">RSRP</a-radio>
                    <a-radio value="SINR">SINR</a-radio>
                    <a-radio value="NO">不显示</a-radio>
                  </a-radio-group>
                </template>
                <img src="../../../../static/img/point.png"/>
              </a-popover>
            </div>
            <a-popover placement="leftTop" trigger="click">
              <template slot="title">
                <span>图例</span>
                <a-icon type="plus-square" theme="twoTone" style="float: right;margin-left: 5px;font-size: 14px" @click="changeLegendNum('plus')" />
                <a-icon type="minus-square" theme="twoTone" style="float: right;margin-left: 5px;font-size: 14px"  @click="changeLegendNum('minus')" />
                <a-icon type="check-square" theme="twoTone" style="float: right;font-size: 14px" @click="saveLegend" />
              </template>
              <template slot="content">
                <div :style="{ margin: '3px 0px' }">
                  <a-radio-group name="radioGroup" v-model="legendRadio" @change="changeLegendType">
                    <a-radio value="RSRP">RSRP</a-radio>
                    <a-radio value="SINR">SINR</a-radio>
                    <a-radio value="UL">UL</a-radio>
                    <a-radio value="DL">DL</a-radio>
                  </a-radio-group>
                </div>
                <div v-for="item in legends[legendRadio].index">
                  <a-input  type="color" style="width: 40px" v-model="legends[legendRadio].content[item-1].colorVal"
                            @change="value => setVal(value, item,'colorVal')" />-----
                  <a-input  style="width: 40px"  v-model="legends[legendRadio].content[item-1].minVal"
                            @change="value => setVal(value, item,'minVal')" />-----
                  <a-input  style="width: 40px" v-model="legends[legendRadio].content[item-1].maxVal"
                            @change="value => setVal(value, item,'maxVal')" />
                </div>
              </template>
              <div class="leaflet-legend">
                <img src="../../../../static/img/legend.png"/>
              </div>
            </a-popover>
          </div>
        </a-card>
        <a-card size="small" title="LTE Serving+Neighboring Cells">
          <a href="#" slot="extra">more</a>
          <div style="height: 27vh;overflow: auto">
          <a-table bordered size="small" :rowClassName="rowClassName"
                   :rowKey="(record,index) => index"
                   :pagination="false"
                   :columns="LTEColumns"
                   :dataSource="LTEData"
          ></a-table>
          </div>
        </a-card>
      </a-col>
      <a-col :span="8">
        <div v-if="!ifShowLog">
          <a-card size="small" title="L3 MESSAGE1">
            <div slot="extra">
              <div style="padding: 3px;background: #fff">
                <a-input style="width: 150px" v-model="messageTypeFlag" @change="messageTypeIndex=0" />
                <a-button type="primary" size="small" @click="queryFlag('message')" >查询</a-button>
              </div>
            </div>
            <scroll-loader @scroll-to-top="loadMoreMessageUp" @scroll-to-botton="loadMoreMessageDown"
                           :minHeight="minHeight"
                           class="container-main" :style="{maxHeight: '40vh'}">
              <a-table bordered size="small" :rowClassName="rowClassNameMsg"
                       :rowKey="record => record.id" :loading="msgLoading"
                       :pagination="false"
                       :columns="msgColumns"
                       :dataSource="msgData"
                       :customRow="msgCustomRow"
              >
                <div slot="icon" slot-scope="record">
                  <a-icon type="arrow-up" v-if="record.mSingaType=='ul'"
                          style="color: red;font-size: 12px;cursor: pointer"></a-icon>
                  <a-icon type="arrow-down" v-if="record.mSingaType=='dl'"
                          style="color: green;font-size: 12px;cursor: pointer"></a-icon>
                </div>
              </a-table>
            </scroll-loader>
          </a-card>
          <a-card size="small" title="Event">
            <div slot="extra">
              <div style="padding: 3px;background: #fff">
                <a-input style="width: 150px" v-model="eventTypeFlag"  @change="eventTypeIndex=0" />
                <a-button type="primary" size="small" @click="queryFlag('event')">查询</a-button>
              </div>
            </div>
            <scroll-loader1 @scroll-to-top="loadMoreEventUp" @scroll-to-botton="loadMoreEventDown"
                            :minHeight="minHeight"
                            class="container-main" :style="{maxHeight: '42vh'}">
              <a-table bordered size="small" :rowClassName="rowClassNameEvent"
                       :rowKey="record => record.mTimestamp" :loading="eventLoading"
                       :pagination="false"
                       :columns="eventColumns"
                       :dataSource="eventData"
                       :customRow="eventCustomRow"
              ></a-table>
            </scroll-loader1>
          </a-card>
        </div>
        <!--<scroll-loader v-if="ifShowLog" @scroll-to-top="loadMoreMessageUp" @scroll-to-botton="loadMoreMessageDown"-->
                       <!--:minHeight="minHeightLog"-->
                       <!--class="container-main" :style="{maxHeight: '86vh'}">-->
          <log v-if="ifShowLog" :mainLogData="mainLogData"></log>
        <!--</scroll-loader>-->
      </a-col>
    </a-row>
    <div class="spin-contain" v-if="loading">
      <a-spin tip="Loading..." size="large">
      </a-spin>
    </div>
    <message-info ref="messageInfo" :record="msgRecord"></message-info>
  </div>
</template>

<script>

  let map;
  let marker;
  var myIcon = L.icon({
    iconUrl: '../../../../static/img/cell.png',
    iconSize: [15, 30],
  });
  let polyLineGroups = new L.layerGroup();
  let cellsGroups = new L.layerGroup();
  let circleGroups = new L.layerGroup();
  let fullCellsGroup = new L.layerGroup();
  let rsrpLabelsGroup = new L.layerGroup();
  let sinrLabelsGroup = new L.layerGroup();
  import L from 'leaflet'
  import "leaflet/dist/leaflet.css"
  import {OutdoorTestApi} from '@/http/api/outdoorTest'
  import messageInfo from '@/views/outdoorTest/children/children/messageInfo.vue'
  import {mapUtils} from '@/utils/map.js'
  import scrollLoader from '@/components/scrollLoader.vue'
  import scrollLoader1 from '@/components/scrollLoader1.vue'
  import log from '@/views/outdoorTest/children/children/log.vue'
  import 'leaflet-rotatedmarker'
  import '../../../../static/css/Leaflet.PolylineMeasure.css'
  export default {
    name: '',
    components: {
      scrollLoader,
      scrollLoader1,
      messageInfo,
      log,
    },
    props:['rowData','ifShowLog'],
    data() {
      return {
        minHeight:37,
        eventType:['FTPConnectionSuccess'],
        //选中的两种方式，表格模糊查询，以及点击地图中circle默认高亮第一个，
        rowClassNameMsg:(record,index)=> {
          if(this.whichHighLightMsg&&this.highLightindexsMsg.length){
            let className = JSON.stringify(this.msgData[index]) == JSON.stringify(this.msgTableData[this.lightFirIndexMsg]) ?
              'shallow' : '';
            return className
          }else{
            let className = record.mMeaasge == this.msgRecord.mMeaasge?
              'shallow' : '';
            return className
          }
        },
        rowClassNameEvent: (record, index) => {
          if(this.whichHighLightEvent&&this.highLightindexsEvent.length){
            let className = JSON.stringify(this.eventData[index]) == JSON.stringify(this.eventTableData[this.lightFirIndexEvent]) ?
              'shallow' : '';
            return className
          }else{
            let className = record.mTimestamp == this.eventRecord.mTimestamp?
              'shallow' : '';
            return className
          }
        },
        rowClassName:(record,index)=>{
          let className = index==0 ? 'shallow': '';
          return className
        },
        map_config: {
          zoom: 13,
          center: [32.03956857467441, 118.63768386683661],
          minZoom: 6,
          maxZoom: 18,
          key: "650887dd02999ce19f403b5a805d2605"
        },
        msgColumns: this.$store.state.constant.msgColumns,
        msgData: [],
        eventColumns: this.$store.state.constant.eventColumns,
        eventData: [],
        LTEColumns: this.$store.state.constant.LTEColumns,
        LTEData: [],
        msgTableData: [],
        eventTableData: [],
        mainLogData:[],
        msgIndexDown: 0,
        msgIndexUp: 0,
        eventIndexDown: 0,
        eventIndexUp: 0,
        msgLoading: false,
        ifLoadEvent: false,
        eventLoading: false,
        loading: false,
        latlngArray: [],
        highLightindexsMsg:[],
        highLightindexsEvent:[],
        lightFirIndexMsg:0,
        lightLaIndexMsg:50,
        lightFirIndexEvent:0,
        lightLaIndexEvent:50,
        msgRecord:{},
        eventRecord:{},
        msgCustomRow: (record,index) => {
          return {
            props: {

            },
            on: { // 事件
              click: (event) => {
                if(event.target.localName=='td'){
                  this.whichHighLightMsg = false;
                  this.whichHighLightEvent = true;
                  this.msgRecord = record;
                  this.getHighLightList(record.latitude,record.longitude,"eventTableData",'highLightindexsEvent','lightFirIndexEvent','lightLaIndexEvent','eventData','eventIndexUp','eventIndexDown');
                  this.getLTEList(record.latitude,record.longitude);
                  this.highLightPoint(record.latitude,record.longitude);
                  this.renderPolyLines(record.latitude,record.longitude);
                }else{
                  this.$refs.messageInfo.getMessageInfo();
                }
              }
            },
          }
        },
        eventCustomRow:(record,index) => {
          return {
            props: {

            },
            on: { // 事件
              click: () => {
                this.whichHighLightEvent = false;
                this.whichHighLightMsg = true;
                this.eventRecord = record;
                this.getHighLightList(record.latitude,record.longitude,"msgTableData",'highLightindexsMsg','lightFirIndexMsg','lightLaIndexMsg','msgData','msgIndexUp','msgIndexDown');
                this.getLTEList(record.latitude,record.longitude);
                this.highLightPoint(record.latitude,record.longitude);
                this.renderPolyLines(record.latitude,record.longitude);
              }
            },
          }
        },
        latlngIndex:0,
        latlngTimeOut:null,
        makerCluster:[],
        cluster:null,
        rollBackName:'play-circle',
        flag:false,
        percent:0,
        speed:1,
        normalEvents:this.$store.state.constant.normalEvents,
        abNormalEvents:this.$store.state.constant.abNormalEvents,
        pointSize:2,
        messageTypeFlag:'',
        eventTypeFlag:'',
        whichHighLightMsg:true,  //判断高亮的方式，点击circle高亮还是点击表格高亮的情况
        whichHighLightEvent:true,  //判断高亮的方式，点击circle高亮还是点击表格高亮的情况
        messageTypeIndex:0,
        eventTypeIndex:0,
        legendRadio:'RSRP',
        legends:{
          'RSRP':{content:[],index:0},
          'SINR':{content:[],index:0},
          'UL':{content:[],index:0},
          'DL':{content:[],index:0}
        },
        kpiValue:'NO'
      }
    },
    computed:{

    },
    beforeCreate() {

    },
    mounted() {
      this.initMap();
      this.queryMainLog();
      this.initLegend();
    },
    methods: {
      initMap() {
        map = L.map("map", {
          center: this.map_config.center,
          zoom: this.map_config.zoom
        });
        mapUtils.addLayer(map,this.map_config,"TianDiTu.Normal.Map");
        mapUtils.addLayer(map,this.map_config,"TianDiTu.Normal.Annotion")
        L.control.scale({maxWidth: 50, metric: true, imperial: false, position: 'bottomleft'}).addTo(map);
        L.control.polylineMeasure ({
          position:'topleft',
          unit:'metres',
          showBearings:false,
          clearMeasurementsOnStop: false,
          showClearControl: true,
          showUnitControl: false}).addTo (map);
        map.on("moveend", (e) => {
          const zoom = map.getZoom();
          if (zoom > 11) {
            this.renderCells();
          }else{
            cellsGroups.clearLayers()
          }
        });
        map.on("zoomend", (e) => {
          const zoom = map.getZoom();
          if (zoom < 12&&this.makerCluster.length>0) {
            fullCellsGroup.addTo(map);
          }else{
            map.removeLayer(fullCellsGroup)
          }
        })
      },
      async queryMainLog() {
        this.loading = true;
        const params = {
          ids: this.selectedRowKeys + "",
          type: "logmain",
          index: "logmain",
          pid: this.rowData.id,
          termMap: {"pid.keyword": this.rowData.id},
          limit: 1000,  //固定
          sortMap: {"testTime": "ASC"},  //固定
          revelFields: ["longitude", "latitude", "testTime", "id", "cellName", "sinr", "rsrp","formatTimestamp",
            "cI", "tAC", "pci", "earfcn", "mSignaBean", "mSignaEventBean", "doorDataInfoBeans",
            "netWorkType","eNB","sPEED","downLoadSpeed","upLoadSpeed","normalEventType","abNormalEventType"],  //固定
          revelPk: "false"  //固定

        }
        const data = await OutdoorTestApi.queryMainLog(params);
        const latlngs = [];
        if (data.data) {
          this.mainLogData = data.data;
          data.data.some((x, index) => {
            if (x.mSignaBean.length > 0) {
              x.mSignaBean.map(m => {
                m.latitude = Number(x.latitude);
                m.longitude = Number(x.longitude);
              })
              this.msgTableData.push(...x.mSignaBean);   //获取msg所有表格数据
            }
            if (x.mSignaEventBean.length > 0) {
              x.mSignaEventBean.map((m,index) => {
                m.latitude = Number(x.latitude);
                m.longitude = Number(x.longitude);
              })
              this.eventTableData.push(...x.mSignaEventBean);   //获取event所有表格数据
            }
            latlngs.push({
              latitude: x.latitude,
              longitude: x.longitude,
              rsrp:x.rsrp,
              sinr:x.sinr
            })   //获取所有的经纬度信息
          });
          //对经纬度数据进行去重操作
          this.latlngArray = [];
          latlngs.map(i => {
            return Object.keys(i).map(k => i[k]).join('')
          }).filter((item, index, strarr) => {
            if (strarr.indexOf(item) === index) {
              this.latlngArray.push(latlngs[index])
            }
          });
          this.msgData = this.msgTableData.slice(0, 50);
          this.eventData = this.eventTableData.slice(0, 50);
          const latitude = data.data[data.data.length - 1].latitude;
          const longitude = data.data[data.data.length - 1].longitude;
          this.getLTEList(latitude, longitude);
          this.initPoints(this.latlngArray);
        }
        this.renderFullCells()
      },
      async getLTEList(latitude, longitude) {
        const params = {
          type: "logmain",
          index: "logmain",
          limit: 1,
          termMap: {
            "longitude.keyword": longitude,  //经度
            "latitude.keyword": latitude,  //纬度
            "pid.keyword": this.rowData.id
          },  //室外测试得主键id
          sortMap: {"testTime": "ASC"},
          revelFields: ["id", "longitude", "latitude", "cellName", "sinr", "rsrp", "cI", "tAC", "pci", "cELLID", "eNB", "earfcn", "doorDataInfoBeans", "testTime"]
        }
        const data = await OutdoorTestApi.queryList(params);
        if(data.data.length){
          this.LTEData = [];
          const pcell = {
            id: "PCell",
            cellName: data.data[0].cellName,
            sinr: data.data[0].sinr,
            rsrp: data.data[0].rsrp,
            ci: data.data[0].cI,
            tac: data.data[0].tAC,
            pci: data.data[0].pci,
            earfcn: data.data[0].earfcn
          }
          this.LTEData.push(pcell);
          data.data[0].doorDataInfoBeans.mLteNeighborhoodInfos.map(x => {
            this.LTEData.push({
              id: 'NCell',
              cellName: '',
              sinr: data.data[0].doorDataInfoBeans.sinr,
              rsrp: data.data[0].doorDataInfoBeans.rsrp,
              ci: x.mLteNeighborhoodCI,
              tac: x.mLteNeighborhoodTAC,
              pci: x.mLteNeighborhoodPCI,
              earfcn: data.data[0].doorDataInfoBeans.earfcn
            })
          })
        }
        this.loading = false;
      },
      loadMoreMessageDown(done) {
        this.msgLoading = true;
        setTimeout(() => {
          const length = this.msgTableData.length;
          this.msgLoading = false;
          this.msgIndexDown++;
          if (((this.msgIndexDown+1) * 50) < length) {
            this.msgData = this.msgData.concat(this.msgTableData.slice(this.lightLaIndexMsg, (this.msgIndexDown+1) * 50));
            this.lightLaIndexMsg = (this.msgIndexDown+1) * 50;
          } else {
            const flag = mapUtils.ifExsist(this.msgData,this.msgTableData[this.msgTableData.length-1]);
            if(!flag){
              this.msgData = this.msgData.concat(this.msgTableData.slice(this.lightLaIndexMsg, length))
            }
            this.lightLaIndexMsg = length;
          }
        }, 500);
        done(false);
      },
      //向上拉加载
      loadMoreMessageUp(done){
        this.msgLoading = true;
        setTimeout(() => {
          const length = this.msgTableData.length;
          this.msgLoading = false;
          this.msgIndexUp--;
          if (this.msgIndexUp > 0) {
            this.msgData = this.msgTableData.slice(this.msgIndexUp * 50, this.lightFirIndexMsg).concat(this.msgData);
          } else {
            const flag = mapUtils.ifExsist(this.msgData,this.msgTableData[0]);
            if(!flag) {
              this.msgData = this.msgTableData.slice(0, this.lightFirIndexMsg).concat(this.msgData);
            }
          }
        }, 500);
        done();
      },
      loadMoreEventUp(done){
        this.eventLoading = true;
        setTimeout(() => {
          const length = this.eventTableData.length;
          this.eventLoading = false;
          this.eventIndexUp--;
          if (this.eventIndexUp > 0) {
            this.eventData = this.eventTableData.slice(this.eventIndexUp * 50, this.highLightindexsEvent[0]).concat(this.eventData);
          } else {
            const flag = mapUtils.ifExsist(this.eventData,this.eventTableData[0]);
            if(!flag) {
              this.eventData = this.eventTableData.slice(0, this.highLightindexsEvent[0]).concat(this.eventData);
            }
          }
        }, 500);
        done();
      },
      loadMoreEventDown(done){
        this.eventLoading = true;
        setTimeout(() => {
          const length = this.eventTableData.length;
          this.eventLoading = false;
          this.eventIndexDown++;
          if ((this.eventIndexDown * 50) < length) {
            this.eventData = this.eventData.concat(this.eventTableData.slice(this.highLightindexsEvent[this.highLightindexsEvent.length - 1], this.eventIndexDown * 50));
          } else {
            const flag = mapUtils.ifExsist(this.eventData, this.eventTableData[this.eventTableData.length - 1]);
            if (!flag) {
              this.eventData = this.eventData.concat(this.eventTableData.slice(this.highLightindexsEvent[this.highLightindexsEvent.length - 1], length))
            }
          }
        }, 500);
        done();
      },
      initPoints(latlngs) {
        if(circleGroups){
          circleGroups.clearLayers();
        }
        map.setView([latlngs[latlngs.length - 1].latitude, latlngs[latlngs.length - 1].longitude], 16);
        latlngs.map(x => {
          const circle = L.circle([x.latitude, x.longitude], {
            color: 'red',
            fillColor: '#f03',
            fillOpacity: 0.50,
            radius: this.pointSize
          }).addTo(circleGroups);
          const rsrp = L.divIcon({
            html: x.rsrp,
            className: 'my-div-icon',
            iconSize:20
          });
          const sinr = L.divIcon({
            html: x.sinr,
            className: 'my-div-icon',
            iconSize:20
          });
          L.marker([x.latitude, x.longitude], { icon: rsrp }).addTo(rsrpLabelsGroup);
          L.marker([x.latitude, x.longitude], { icon: sinr }).addTo(sinrLabelsGroup);


          circle.on('click', (e) => {
            this.whichHighLightMsg = true;
            this.whichHighLightEvent = true;
            this.getLTEList(e.latlng.lat, e.latlng.lng);
            this.getHighLightList(e.latlng.lat, e.latlng.lng, 'msgTableData','highLightindexsMsg','lightFirIndexMsg','lightLaIndexMsg', 'msgData','msgIndexUp','msgIndexDown');
            this.getHighLightList(e.latlng.lat, e.latlng.lng, 'eventTableData','highLightindexsEvent', 'lightFirIndexEvent','lightLaIndexEvent', 'eventData','eventIndexUp','eventIndexDown');
            this.renderPolyLines(e.latlng.lat, e.latlng.lng)
          })
        })
        circleGroups.addTo(map);
      },
      //点击地图上的circle高亮，默认高亮对应数据的第一条
      getHighLightList(lat, lng, tableData,highLight, lightFirst,lightLast, data,indexUp,indexDown) {
        this[highLight] = [];
        this[tableData].filter((x, index) => {
          if (lat == x.latitude && lng == x.longitude) {
            this[highLight].push(index);
          }
        });
        if (this[highLight].length > 0) {
          this[lightFirst] = this[highLight][0];
          this[lightLast] = this[highLight][this[highLight].length-1];
          if (this[lightFirst] + 50 < this[tableData].length) {
            this[data] = this[tableData].slice(this[lightFirst], this[lightFirst] + 50)
          } else {
            this[data] = this[tableData].slice(this[lightFirst], this[tableData].length)
          }
          this[indexUp] = parseInt(this[lightFirst] / 50);
          this[indexDown] = parseInt(this[lightFirst] / 50);
        } else {
//          this[lightFirst] = 50;
          this[lightLast] = 50;
          this[data] = this[tableData].slice(0, 50)
          this[indexUp] = 0
          this[indexDown] = 0
        }
      },
      highLightPoint(lat, lng) {
        mapUtils.removeLayer(marker,map);
        marker = L.circle([lat, lng],
          {
            color: 'blue',
            fillColor: '#ADD8E6',
            fillOpacity: 1,
            radius: this.pointSize
          }).addTo(map);
        map.setView([lat, lng]);
      },
      //根据左下右上经纬度渲染主服务小区和邻区扇去
      async renderCells() {
        //左上右下经纬度拼接
        const leftup = map.getBounds().getNorthWest().lng + "_" + map.getBounds().getNorthWest().lat;
        const rightdown = map.getBounds().getSouthEast().lng + "_" + map.getBounds().getSouthEast().lat;

        const latlngs = leftup + "_" + rightdown
        const params = {
          type: "simgc",  //固定
          pid: latlngs,  //var strModel=bsswlng+"_"+bsnelat+"_"+bsnelng+"_"+bsswlat;
          index: "simgc",//固定
          limit: 1000,//固定
          termMap: {},//固定
          revelFields: ["lte_azimuth", "lte_ci", "lte_longitude2", "lte_latitude2"],//固定
          revelPk: "false"//固定
        }
        const data = await OutdoorTestApi.boundingBoxQuery(params);
        if (cellsGroups) {
          cellsGroups.clearLayers()
        }
        data.data.map(x => {
          const cells = L.marker([x.lte_latitude2, x.lte_longitude2],
            {icon: myIcon, rotationAngle: x.lte_azimuth}).addTo(cellsGroups);
        })
        cellsGroups.addTo(map);
      },
      //点击连线
      async renderPolyLines(lat, lng) {
        if (polyLineGroups) {
          polyLineGroups.clearLayers()
        }
        const params = {
          longitude: lng,//经度
          latitude: lat,//纬度
          mainLogId: this.rowData.id//室外测试得id
        }
        const data = await OutdoorTestApi.queryMainService(params);
        data.data.map(x => {
          let res = [[lat, lng], [x.lte_latitude2, x.lte_longitude2]];
          L.polyline(res, {color: 'red'}).addTo(polyLineGroups);
        })

        const paramsNears = {
          "type": "simgc",//固定
          "index": "simgc",//固定
          "limit": 1,//固定
          "pid": lng + "_" + lat,
          "termMap": {"lte_phycellid.keyword": "'+linquPci+'"}  //PCI的值
        }
        const dataNear = await OutdoorTestApi.queryNearPoint(paramsNears);
        let latlngs = [];
        dataNear.data.map(x => {
          latlngs = [[lat, lng], [x.location.lat, x.location.lon]];
          L.polyline(latlngs, {color: 'red'}).addTo(polyLineGroups);
        })
        polyLineGroups.addTo(map);
      },
      //回放
      rollBack() {
        if(this.rollBackName == 'pause-circle'){
          this.rollBackName = 'play-circle';
          clearTimeout(this.latlngTimeOut);
        }else{
          this.playPoints();
          this.rollBackName = 'pause-circle'
        }
      },
      playPoints(){
        this.latlngTimeOut = setTimeout(() => {
          if (this.latlngIndex > this.latlngArray.length - 1) {
            this.latlngIndex = 0;
          }
          this.highLightPoint(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
          this.getLTEList(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
          this.getHighLightList(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude, "msgTableData",'highLightindexsMsg', 'lightFirIndexMsg','lightLaIndexMsg', 'msgData','msgIndexUp','msgIndexDown');
          this.getHighLightList(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude, "eventTableData", 'highLightindexsEvent','lightFirIndexEvent','lightLaIndexEvent', 'eventData','eventIndexUp','eventIndexDown');
          this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
          this.latlngIndex++;
          this.percent = (this.latlngIndex/this.latlngArray.length).toFixed(2)*100;
          this.playPoints();
        }, this.speed*1000);
      },
      //重置
      resetRollBack(){
        clearTimeout(this.latlngTimeOut);
        this.latlngIndex = 0;
        this.percent =0;
        this.rollBackName = 'play-circle';
        this.highLightPoint(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
        this.getLTEList(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
        this.getHighLightList(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude, 'msgTableData','highLightindexsMsg', 'lightFirIndexMsg','lightLaIndexMsg', 'msgData','msgIndexUp','msgIndexDown');
        this.getHighLightList(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude, 'eventTableData','highLightindexsEvent', 'lightFirIndexEvent','lightLaIndexEvent', 'eventData','eventIndexUp','eventIndexDown');
        this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
        this.latlngIndex++;
        this.percent = (this.latlngIndex/this.latlngArray.length).toFixed(2)*100;
      },
      async renderFullCells() {
        const params = {
          "index": "simgc",//固定
          "type": "simgc",//固定
        }
        const data = await OutdoorTestApi.distinctGcLonLat(params);
        this.makerCluster = data.data;
//        this.cluster = mapUtils.createMakerCluster();
        data.data.map(x=>{
//          let maker = mapUtils.createMakerByLatlng(x.lte_latitude2,x.lte_longitude2);
//          this.cluster.addLayer(maker);

          const circle = L.circle([x.lte_latitude2, x.lte_longitude2], {
            color: 'green',
            fillColor: 'green',
            fillOpacity: 0.50,
            radius: 3
          }).addTo(fullCellsGroup);
        })
      },
      changeMarkerSize(e){
        this.pointSize=e.target.value;
        this.initPoints(this.latlngArray);
      },
      queryFlag(e){
        if(e == 'message'){
          this.msgLoading = true;
          const indexs =mapUtils.ToFindAllIndex(this.msgTableData,this.messageTypeFlag,'mMessageType');
          if(indexs.length){
            this.whichHighLightMsg = false;
            this.whichHighLightEvent = true;
            this.getHighLightBySearch(indexs,'msgTableData','highLightindexsMsg','msgData','msgIndexUp','msgIndexDown','messageTypeIndex');
            this.msgRecord = this.msgTableData[indexs[this.messageTypeIndex]];
            this.getHighLightList(this.msgRecord.latitude, this.msgRecord.longitude, 'eventTableData','highLightindexsEvent', 'lightFirIndexEvent','lightLaIndexEvent', 'eventData','eventIndexUp','eventIndexDown');
            this.highLightPoint(this.msgRecord.latitude, this.msgRecord.longitude);
            this.getLTEList(this.msgRecord.latitude, this.msgRecord.longitude);
            this.renderPolyLines(this.msgRecord.latitude, this.msgRecord.longitude);
//            this.rowClassNameMsg(this.msgTableData[indexs[this.messageTypeIndex]],indexs[this.messageTypeIndex]);
            this.messageTypeIndex++;
            this.lightFirIndexMsg = indexs[this.messageTypeIndex];
          }
          this.msgLoading = false;
        }else{
          this.eventLoading = true;
          const indexs =mapUtils.ToFindAllIndex(this.eventTableData,this.eventTypeFlag,'mEventType');
//          const
          if(indexs.length){
            this.whichHighLightEvent = false;
            this.whichHighLightMsg = true;
            this.getHighLightBySearch(indexs,'eventTableData','highLightindexsEvent','eventData','eventIndexUp','eventIndexDown','eventTypeIndex');
            this.eventRecord = this.eventTableData[indexs[this.eventTypeIndex]];
            this.getHighLightList(this.eventRecord.latitude, this.eventRecord.longitude, 'msgTableData','highLightindexsMsg', 'lightFirIndexMsg','lightLaIndexMsg', 'msgData','msgIndexUp','msgIndexDown');
            this.highLightPoint(this.eventRecord.latitude, this.eventRecord.longitude);
            this.getLTEList(this.eventRecord.latitude, this.eventRecord.longitude);
            this.renderPolyLines(this.eventRecord.latitude, this.eventRecord.longitude);
//            this.rowClassNameEvent(this.eventTableData[indexs[this.eventTypeIndex]],indexs[this.eventTypeIndex]);
            this.eventTypeIndex++;
            this.lightFirIndexEvent = indexs[this.eventTypeIndex];
          }
          this.eventLoading = false;
        }
      },
      //根据搜索框查询type
      getHighLightBySearch(indexs, tableData, highLight, data,indexUp,indexDown,typeIndex) {
        if(this[typeIndex]>indexs.length-1){
          this[typeIndex] = 0;
        }
        this[highLight] = indexs;
        if (this[highLight].length > 0) {
          if (this[highLight][this[typeIndex]] + 50 < this[tableData].length) {
            this[data] = this[tableData].slice(this[highLight][this[typeIndex]], this[highLight][this[typeIndex]] + 50)
          } else {
            this[data] = this[tableData].slice(this[highLight][this[typeIndex]], this[tableData].length)
          }
          this[indexUp] = parseInt(this[highLight][this[typeIndex]] / 50);
          this[indexDown] = parseInt(this[highLight][this[typeIndex]] / 50);
        } else {
          this[data] = this[tableData].slice(0, 50)
          this[indexUp] = 0
          this[indexDown] = 0
        }
      },

      //保存legend
      async saveLegend(){
        let params = [];
        Object.keys(this.legends).map(m=>{
          params.push({
            item: m,
            content: this.legends[m].content
          })
        })
        const data = await OutdoorTestApi.saveLegend(params);
        if(data.data.success){
         this.$message.success("图例修改成功！")
        }
      },
      //初始化图例信息
      async initLegend(){
        const params = {
          item:this.legendRadio,
        }
        const data = await OutdoorTestApi.queryLegend(params);
        if(data.data.object.length){
          this.legends[this.legendRadio].content = data.data.object[0].content;
          this.legends[this.legendRadio].index = data.data.object[0].content.length;
        }
      },
      setVal(e,index,key){
        this.legends[this.legendRadio].content[index-1][key]=e.target.value;
        /*实时更新视图*/
        this.$set(this.legends[this.legendRadio].content, index-1, this.legends[this.legendRadio].content[index-1]);
      },
      changeLegendNum(e) {
        if (e == 'plus') {
          this.legends[this.legendRadio].index++;
          this.legends[this.legendRadio].content[this.legends[this.legendRadio].index - 1] = {
            colorVal: '',
            minVal: '',
            maxVal: ''
          }
        } else {
          const release = this.legends[this.legendRadio].content[this.legends[this.legendRadio].index - 1] //从后往前删除
          const index = this.legends[this.legendRadio].index - 1;
          this.legends[this.legendRadio].content.splice(index,1);
          this.legends[this.legendRadio].index--;
        }
      },
      changeLegendType(){
        this.initLegend();
      },
      changeKpiValue(e){
        if(e.target.value == 'RSRP'){
          map.removeLayer(sinrLabelsGroup)
          rsrpLabelsGroup.addTo(map);
        }else if(e.target.value == 'SINR'){
          map.removeLayer(rsrpLabelsGroup)
          sinrLabelsGroup.addTo(map);
        } else {
          map.removeLayer(sinrLabelsGroup)
          map.removeLayer(rsrpLabelsGroup)
        }
      }
    }
  }
</script>

<style>
  @import "../../../../static/css/style.css";
  .my-div-icon{
    font-size:12px;
    /*background:red;*/
    /*width:5px;*/
    color:blue;
  }

  #outdoorTest .ant-btn-default {
    background: rgb(105, 116, 124);
    color: white;
  }
  #outdoorTest .ant-btn-default:hover {
    background: #586268;
    color: white;
  }
  .spin-contain{
    position: absolute;
    top: 40%;
    z-index:9999;
    left:50%;
    border: 1px solid #91d5ff;
    background-color: #e6f7ff;
    padding: 10px 30px;
  }
  .shallow{
    background:#e6f7ff;
    font-weight: bold;
  }
  .ant-card-extra{
    width: 70%;
  }
  .ant-progress-line{
    width:50%
  }
  .leaflet-marker{
    top: 160px;
    left: 10px;
    background: white;
  }
  .leaflet-marker:hover{
    background:  rgba(150,150,150,0.1);
  }


  .ivu-select-default.ivu-select-multiple .ivu-select-selection{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  .ivu-select-group-title{
    text-align: left;
    font-weight: bold;
  }
  .leaflet-legend{
    position: absolute;
    right: 0.5%;
    bottom: 4%;
    z-index: 999;
    cursor: pointer;
  }
</style>



