import axios from 'axios'
// import { throwErr } from '@/utils/throwErr' //utils 捕捉服务端http状态码的方法
import store from '@/store'   //引入vuex的相关操作
import { message } from 'ant-design-vue' //element Toast的提示
import router from '@/router'

/**
 * 提示函数
 * 禁止点击蒙层、显示一秒后关闭
 */
const tip = msg => {
  message.warning(msg)
}
const tipError = msg => {
  message.error(msg)
}
const tipSuccess = msg => {
  message.success(msg)
}

/**
 * 跳转登录页
 * 携带当前页面路由，以期在登录页面完成登录后返回当前页面
 */
const toLogin = () => {
  router.replace({
    path: '/',
    query: {
      redirect: router.currentRoute.fullPath
    }
  });
}

/**
 * 请求失败后的错误统一处理
 * @param {Number} status 请求失败的状态码
 */
const errorHandle = (status, other) => {
  // 状态码判断
  switch (status) {
    // 401: 未登录状态，跳转登录页
    case 401:
      toLogin()
      break
    // 403 token过期
    // 清除token并跳转登录页
    case 403:
      tip('登录过期，请重新登录')
      // localStorage.removeItem('token')
      // store.commit('loginSuccess', null);
      setTimeout(() => {
        toLogin()
      }, 1000)
      break
    // 404请求不存在
    case 404:
      tip('请求的资源不存在')
      break
    default:
  }
}

const toastHandle = (data) => {
  const { errorCode, errorMsg } = data;
  if (errorCode === '0002') {
    tipError('接口异常，请联系管理员！')
  } else if (errorCode === '0001') {
    tip(errorMsg)
    if (errorMsg === '请重新登录!') {
      toLogin()
    }
  } else if (errorCode === '0000') {
    if (errorMsg !== '查询成功' && errorMsg !== '列表查询成功!' && errorMsg !== '查询成功!') {
      tipSuccess(errorMsg)
    }
  }
}
// 过滤请求
let token = '';
axios.defaults.withCredentials = false;
axios.defaults.headers.common['token'] = token;
axios.interceptors.request.use(config => {
  // config 为请求的一些配置 例如：请求头 请求时间 Token  可以根据自己的项目需求个性化配置，参考axios的中文说明手册  自己多动动手
  // 由于我们项目的后端大大给力，很多东西在服务端帮我们处理好了所以请求阶段只要传好参数就好了
  // config.timeout = 30 * 1000 // 请求响应时间
  let userToken = localStorage.getItem("token");
  if (userToken) {
    token = userToken;
  }
  config.headers.common['token'] = token;
  //console.dir(config);
  return config
}, error => {
  return Promise.reject(error)
})
