<script>
  export default {
    components: {
    },
    data: function () {
      return {
        collapsed: false,
        rootSubmenuKeys: ['sub0','sub1', 'sub2', 'sub3','sub4','sub5'],
        openKeys: ['sub0'],
        username: '',
        roleName: '',
        area: '',
        loginArea: '',
        announceContent:'',
        userPermissionList:[],
      }
    },
    computed: {
      defaultSelected() {
        if (this.$route.name != null) {
          return [this.$route.name]
        }
      }
    },
    mounted() {
//      this.userPermissionList = JSON.parse(localStorage.getItem('userPermissionList'))
//      this.username = localStorage.getItem('username')
//      this.roleName = localStorage.getItem('roleName')
//      this.area = localStorage.getItem('area');
//      this.loginArea = localStorage.getItem('loginArea');
//      this.announceContent = localStorage.getItem('announceContent');
    },
    methods: {
      toIndexPage(){
        this.$router.push({
          path: 'projectManage'
        })
      },
      selectItem({item, key, keyPath}) {
        this.$router.push({name: key})
      },
      onOpenChange(openKeys) {
        const latestOpenKey = openKeys.find(key => this.openKeys.indexOf(key) === -1)
        if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
          this.openKeys = openKeys
        } else {
          this.openKeys = latestOpenKey ? [latestOpenKey] : []
        }
      },
      toggleCollapsed() {
        this.collapsed = !this.collapsed
      },
      logout() {
        this.$confirm({
          title: '是否要退出系统',
          content: '你确定要退出系统吗?',
          onOk: () => {
            this.$api.userManage.userLogout().then(x => {
              //成功的逻辑
              localStorage.clear();
              this.$router.push({path: '/'});
            })
          }
        })

      }
    }
  }
</script>

<style>
  #components-layout {
    height: 100%;
    width: 100%;
  }
  #components-layout .trigger {
    font-size: 18px;
    line-height: 45px!important;
    padding: 0 24px;
    cursor: pointer;
    transition: color .3s;
  }
  #components-layout .trigger:hover{
    background:#eeeeee ;
  }
  #components-layout .logo {
    height: 32px;
    margin: 30px 0px 50px 0px;
    color:white;
    font-weight: bold;
    font-size: 20px;
    letter-spacing:2px
  }
  #components-layout .menu{
    text-align: left;
    color:#7f99ac;
    margin-left: 35px;
    font-size: 16px;
  }
  .ant-layout-sider{
    background: #2b3d4b!important;
  }
  .ant-menu-item, .ant-menu-submenu-title{
    color: #7f99ac!important;
    font-size: 14px;
  }
  .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected{
    background-color: #273744!important;
    color: white!important;
  }
  .ant-menu-item:hover,.ant-menu-submenu-title:hover{
    background-color: #273744!important;
    color: white!important;
  }
  .ant-menu-submenu > .ant-menu{
    background: #2b3d4b!important;
  }
  .ant-layout-header{
    height: 46px!important;
    line-height: 46px!important;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .user{
    margin-left: auto;
    margin-right: 10px;
    color: #434a57;
    letter-spacing:1px
  }
  .ant-layout.ant-layout-has-sider > .ant-layout, .ant-layout.ant-layout-has-sider > .ant-layout-content{
    overflow: hidden;
  }
  .content{
    margin: 5px;
    padding: 10px 3px 10px 10px;
    background: #fff;
    height:calc(100% - 46px);
    overflow:auto
  }
  .ant-menu-inline .ant-menu-item, .ant-menu-inline .ant-menu-submenu-title{
    font-size: 1.2em;
  }
</style>






