<template>
  <div style="height:86vh;overflow: auto"
    v-infinite-scroll="loadMoreDown"
    infinite-scroll-disabled="switchForMore"
    infinite-scroll-distance="10">
    <a-table bordered size="small" :rowClassName="rowClassName"
             :rowKey="(record,index) => index" :loading="loading"
             :pagination="false"
             :columns="logColumns"
             :dataSource="logData"
    ></a-table>
  </div>

</template>

<script>
  export default {
    name: '',
    components: {
    },
    props:{
      mainLogData:Array
    },
    data() {
      return {
        minHeightLog:86,
        rowClassName:(record,index)=>{
          let className = index==0 ? 'shallow_gray': '';
          return className
        },
        logColumns:this.$store.state.constant.logColumns,
        logData:[],
        logTableData:[],
        loading:false,
        indexDown:0,
        switchForMore:false,
      }
    },
    beforeCreate() {

    },
    mounted() {
      this.initLogTable();
    },
    methods: {
      initLogTable(){
        this.logData = [];
        console.log(this.mainLogData);
        this.mainLogData.map(x=>{
          let doorObj=[];
          const obj = {
            formatTimestamp:x.formatTimestamp,
            netWorkType:x.netWorkType,
            longitude:x.longitude,
            latitude:x.latitude,
            sPEED:x.sPEED,
            cellName:x.cellName,
            cI:x.cI,
            eNB:x.eNB,
            cELLID:x.cELLID,
            tAC:x.tAC,
            earfcn:x.earfcn,
            pci:x.pci,
            rsrq:x.rsrq,
            rsrp:x.rsrp,
            sinr:x.sinr,
            downLoadSpeed:x.downLoadSpeed,
            upLoadSpeed:x.upLoadSpeed,
            normalEventType:x.normalEventType,
            abNormalEventType:x.abNormalEventType,
          }
          x.doorDataInfoBeans.mLteNeighborhoodInfos.map((v, index) => {
            if(index<6){
              doorObj[index] = {};
              doorObj[index]["mLteNeighborhoodEarfcn" + index] = v.mLteNeighborhoodEarfcn;
              doorObj[index]["mLteNeighborhoodPCI" + index] = v.mLteNeighborhoodPCI;
              doorObj[index]["mLteNeighborhoodRSRPOrSINR" + index] = v.mLteNeighborhoodRSRPOrSINR;
              doorObj[index]["mLteNeighborhoodRsrq" + index] = v.mLteNeighborhoodRsrq;
            }
          })
          let res = Object.assign(obj,...doorObj)
          this.logTableData.push(res)
        })
        this.logData = this.logTableData.slice(0,50);
        console.log(this.logTableData);
      },
      loadMoreDown(){
        this.loading = true;
        setTimeout(() => {
          const length = this.logTableData.length;
          this.loading = false;
          this.indexDown++;
          if ((this.indexDown+1)*50 < length) {
            this.logData = this.logData.concat(this.logTableData.slice((this.indexDown * 50),(this.indexDown+1)*50));
          } else {
            this.logData = this.logData.concat(this.logTableData.slice((this.indexDown * 50),length));
          }
        }, 500);
      }
    }
  }
</script>

<style>
  @import "../../../../../static/css/style.css";
</style>



