import L from 'leaflet'
import "leaflet/dist/leaflet.css"
import * as Provider from "leaflet.chinatmsproviders"; //全局引入
// 引入 leaflet.markercluster
import "leaflet.markercluster/dist/MarkerCluster.css"
import "leaflet.markercluster/dist/MarkerCluster.Default.css"
import "leaflet.markercluster";

// 解决默认 maker 无法显示的问题
import icon from "leaflet/dist/images/marker-icon.png";
import iconShadow from "leaflet/dist/images/marker-shadow.png";
let DefaultIcon = L.icon({
  iconUrl: icon,
  shadowUrl: iconShadow,
});
L.Marker.prototype.options.icon = DefaultIcon;
export const mapUtils = {
  addLayer:(map,map_config,type)=>{
    L.tileLayer.chinaProvider(type, {
      maxZoom: map_config.maxZoom,
      minZoom: map_config.minZoom,
      key: map_config.key
    }).addTo(map);
  },
  createMakerCluster: () => {
    return L.markerClusterGroup();
  },
  createMakerByLatlng:(lat,lng)=>{
    return L.marker([lat,lng],{
      icon:DefaultIcon
    })
  },
  removeLayer:(layers,map)=>{
    if (layers) {
      map.removeLayer(layers);
    }
  },
  clearLayers:(layers,map)=>{
    if (layers) {
      map.clearLayers(layers);
    }
  },

  /*
   * @param x {Object} 对象1
   * @param y {Object} 对象2
   * @return  {Boolean} true 为相等，false 为不等
   */
  ifExsist :  (arr, obj)=> {
    if(JSON.stringify(arr).indexOf(JSON.stringify(obj)) === -1){
      return false;  //不存在
    }else{
     return true //已存在
    }
  },
  //data  数组  找到符合要求的第一个下标
  TofindIndex:(data,flag)=>{
    console.log(data);
    data.findIndex((value, index, arr)=> {
      return value['mMessageType'].indexOf(flag)!=-1
    })
  },
  //根据搜索的内容高亮表格数据
  ToFindAllIndex:(data,flag,type)=>{
    let indexs = [];
    data.map((value, index, arr)=> {
      if(value[type].indexOf(flag)!=-1){
        indexs.push(index);
      }
    })
    return indexs
  },




  scrollUp:(loading,tableData,indexUp,data,highLight) =>{
    loading = true;
    setTimeout(() => {
      const length = tableData.length;
      loading = false;
      indexUp--;
      if (indexUp > 0) {
        data = tableData.slice(indexUp * 10, highLight[0]).concat(data);
      } else {
        data = data.concat(tableData.slice(0, highLight[0]));
      }
    }, 500);
  }

}




