<template>
  <div id="outdoorTest">
    <a-tabs
      hideAdd
      v-model="activeKey"
      type="editable-card"
      @edit="onEdit">
      <a-tab-pane tab="室外测试" key="室外测试" :closable="false">
        <a-row class="topSearch">
          <span>时间选择:</span>
          <a-range-picker v-model="rangeTime"
                          :showTime="{ format: 'HH:mm:ss' }"
                          format="YYYY-MM-DD HH:mm:ss"
                          :placeholder="['Start Time', 'End Time']"
          />
          <span>运营商:</span>
          <a-input v-model="operator" style="width: 80px" placeholder="请输入运营商" />
          <span>城市:</span>
          <a-input v-model="city" style="width: 80px" placeholder="请输入城市" />
          <span>测试人员:</span>
          <a-input v-model="person" style="width: 80px" placeholder="请输入测试人员" />
          <span>手机号:</span>
          <a-input v-model="phone" style="width: 80px" placeholder="请输入手机号" />

          <a-button type="primary"  style="background: rgb(122,110,188)" @click="query">查询</a-button>

          <span style="float: right">
            <a-button-group>
              <a-button type="default" @click="exportOutHomeTest" ><a-icon type="download" />导出</a-button>
              <a-button type="default" @click="deleteOutHome">删除</a-button>

            </a-button-group>
          </span>
        </a-row>
        <a-table bordered size="small" :rowClassName="rowClassName"
                 :rowKey="record => record.id"
                 @change="handleTableChange" :pagination="pagination"
                 :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
                 :columns="outTestColumns"
                 :dataSource="data"
        >
          <div slot="operation" slot-scope="text,record,index">
            <a-button type="primary" size="small" @click="add('统计',record)">统计</a-button>
            <a-button type="primary" size="small" @click="add('查看',record)">查看</a-button>
            <!--<a-icon type="qrcode" size="16" style="cursor: pointer" />|-->
            <!--<a-icon type="file-search" size="16" style="cursor: pointer" />-->
          </div>
        </a-table>
      </a-tab-pane>

      <a-tab-pane v-for="pane in panes" :tab="pane" :key="pane">
        <play-back v-if="activeKey=='查看'" :rowData="rowData" :ifShowLog="ifShowLog"></play-back>
        <count v-if="activeKey=='统计'"></count>
      </a-tab-pane>
      <a-button slot="tabBarExtraContent" v-if="activeKey!='室外测试'" @click="transForm">切换</a-button>
    </a-tabs>
  </div>
</template>

<script>

  import count from './children/count.vue'
  import playBack from './children/playBack.vue'
  import {OutdoorTestApi} from '@/http/api/outdoorTest'
  import moment from 'moment'
  import {downFile} from '@/utils/downFile.js'
  export default {
    name: '',
    components: {
      count,playBack
    },
    data() {
      return {
        rowClassName:(record,index)=>{
          let className = index % 2 ? 'shallow_gray': '';
          return className
        },
        operator:'',
        city:'',
        person:'',
        phone:'',
        outTestColumns:this.$store.state.constant.outTestColumns,
        data:[],
        selectedRowKeys:[],
        activeKey:'室外测试',
        panes: [],
        rangeTime:[],
        pagination: {
          pageSize:10,
          current:1,
          total:0,
          showTotal: total => `共 ${total} 条数据`,
          showSizeChanger: true,
          pageSizeOptions: ['5', '10', '15', '20'],
        },
        rowData:'',
        ifShowLog:false,
      }
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    mounted() {

    },
    methods: {
     async query(){
       const params = {
         beginTime:  this.rangeTime.length == 0 ? '':moment(this.rangeTime[0]).format('YYYY-MM-DD HH:mm:ss'),
         endTime:  this.rangeTime.length == 0 ? '':moment(this.rangeTime[1]).format('YYYY-MM-DD HH:mm:ss'),
         index: 'logouthome',
         type: 'logouthome',
         page: this.pagination.current,
         limit: this.pagination.pageSize,
         termMap: {
           'operatorService.keyword': this.operator,
           'city.keyword': this.city,
           'testPerson.keyword': this.person
         }
       }
       const data = await OutdoorTestApi.queryOutHome(params)
       if(data.data.code==0){
         this.data = data.data.data;
         this.pagination.total = data.data.count;
       }
      },
      handleTableChange(pagination){
        this.pagination = pagination;
        this.query();
      },
      onSelectChange(selectedRowKeys){
        this.selectedRowKeys = selectedRowKeys;
      },
      async deleteOutHome(){
        if(this.selectedRowKeys.length>0){
          const params = {
            ids:this.selectedRowKeys+""
          }
          const data = await OutdoorTestApi.deleteOutHome(params);
        if(data.success){
          this.query();
        }
        }else{
          this.$message.warning('请先选择要删除的行！');
        }
      },
      async exportOutHomeTest(){
        const params={
          ids:this.selectedRowKeys+""
        }
       const data =await OutdoorTestApi.exportOutHomeTest(params);
        let headers = data.headers;
        let contentType = headers['content-type'];
        if (!data.data) {
          return false;
        } else {
          const blob = new Blob([data.data], {type: contentType});

          const contentDisposition = data.headers['content-disposition'];
          let fileName = 'unknown';
          if (contentDisposition) {
            fileName = window.decodeURI(data.headers['content-disposition'].split('=')[1]);
          }
          downFile(blob, "室外测试报告");
        }
      },
      onEdit(targetKey, action) {
        this[action](targetKey)
      },
      remove(targetKey) {
        let activeKey = this.activeKey
        let lastIndex
        this.panes.forEach((pane, i) => {
          if (pane === targetKey) {
            lastIndex = i - 1
          }
        })
        const panes = this.panes.filter(pane => pane !== targetKey)
        if (lastIndex >= 0 && activeKey === targetKey) {
          activeKey = panes[lastIndex]
        } else {
          activeKey = '室外测试'
        }
        this.panes = panes
        this.activeKey = activeKey;

      },
      add(item,data){
        this.rowData = data;
        if(this.panes.indexOf(item)==-1){
          this.panes.push(item)
        }
        this.activeKey = item;
      },
      transForm(){
        this.ifShowLog = !this.ifShowLog;
      }
    }
  }
</script>

<style>
  @import "../../../static/css/style.css";

  #outdoorTest .ant-btn-default {
    background: rgb(105, 116, 124);
    color: white;
  }
  #outdoorTest .ant-btn-default:hover {
    background: #586268;
    color: white;
  }
</style>



