<template>
  <div class="login">
    <div class="login_box">
      <span class="login_box_span">5G单验平台登录</span>
      <a-form :form="form" class="login-form" @submit="login">
        <a-form-item>
          <a-input v-decorator="[ 'userName',{ rules: [{ required: true, message: 'Please input your username!' }] }]"
                   placeholder="Username">
            <a-icon
              slot="prefix"
              type="user"
              style="color: rgba(0,0,0,.25)"
            />
          </a-input>
        </a-form-item>
        <a-form-item>
          <a-input
            v-decorator="['passWord',{ rules: [{ required: true, message: 'Please input your Password!' }] } ]"
            type="password"
            placeholder="Password">
            <a-icon
              slot="prefix"
              type="lock"
              style="color: rgba(0,0,0,.25)"/>
          </a-input>
        </a-form-item>
        <a-form-item>
          <!--<a-checkbox-->
            <!--v-decorator="['remember', {-->
            <!--valuePropName: 'checked',-->
            <!--initialValue: true}]">Remember me-->
          <!--</a-checkbox>-->
          <!--<a-->
            <!--class="login-form-forgot"-->
            <!--href=""-->
          <!--&gt;-->
            <!--Forgot password-->
          <!--</a>-->
          <a-button
            type="primary"
            html-type="submit"
            class="login-form-button"
          >
            登录
          </a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>

<script>
  import {LoginApi} from '@/http/api/login'
  export default {
    components: {},
    name: '',
    data() {
      return {}
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    methods: {
      login(e) {
        e.preventDefault();
        this.form.validateFields((err, values) => {
          if (!err) {
            this.confirmLogin(values)
          }
        });
      },
      async confirmLogin(values){
        const params = {
          username: values.userName,
          password: values.passWord
        }
        const data = await LoginApi.login(this.$qs.stringify(params))
        console.log(data);
//        localStorage.setItem("token",data.data.dataSource.token);
        if(data.data.resultCode==1){
          this.$router.push("/test")
        }
      }
    }
  }
</script>

<style scoped>
  @import "../../static/css/login.css";
</style>



