
import Vue from 'vue'
import App from './App'
import router from './router'
// import {Button,Tabs,DatePicker,Input,Select,Layout,Modal,Upload} from 'ant-design-vue'
import Antd from 'ant-design-vue'
import 'ant-design-vue/dist/antd.css'
import store from './store'
import qs from 'qs'

import md5 from 'js-md5';
import vueiInfinite from 'vue-infinite-scroll'
Vue.prototype.$md5 = md5;
Vue.prototype.$qs = qs;
Vue.use(Antd)
Vue.use(vueiInfinite)

import {Select,OptionGroup,Option} from 'view-design';
import 'view-design/dist/styles/iview.css'
Vue.component('Select', Select);
Vue.component('OptionGroup', OptionGroup);
Vue.component('Option', Option);

// Vue.use(Button,Tabs,DatePicker,Input,Select,Layout,Modal,Upload)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App},
  template: '<App/>'
})



