<template>
  <div id="outdoorTest">
    <a-tabs
      hideAdd
      v-model="activeKey"
      type="editable-card"
      @edit="onEdit">
      <a-tab-pane tab="一键测试" key="一键测试" :closable="false">
        <a-row class="topSearch">
          <span>时间选择:</span>
          <a-range-picker v-model="rangeTime"
            :showTime="{ format: 'HH:mm:ss' }"
            format="YYYY-MM-DD HH:mm:ss"
            :placeholder="['Start Time', 'End Time']"
          />
          <span>运营商:</span>
          <a-input v-model="operator" style="width: 80px" placeholder="请输入运营商" />
          <span>城市:</span>
          <a-input v-model="city" style="width: 80px" placeholder="请输入城市" />
          <span>测试人员:</span>
          <a-input v-model="person" style="width: 80px" placeholder="请输入测试人员" />
          <span>手机号:</span>
          <a-input v-model="phone" style="width: 80px" placeholder="请输入手机号" />

          <a-button type="primary"  style="background: rgb(122,110,188)" @click="query">查询</a-button>

          <a-dropdown-button :trigger="['click']">
            Dropdown
            <a-menu slot="overlay" @click="handleMenuClick">
              <a-menu-item key="GIS">
                <a-icon type="dribbble" />GIS
              </a-menu-item>
              <a-menu-item key="导出">
                <a-icon type="download" />导出
              </a-menu-item>
              <a-menu-item key="删除">
                <a-icon type="delete" />删除
              </a-menu-item>
            </a-menu>
          </a-dropdown-button>
        </a-row>
        <a-table bordered size="small" :rowClassName="rowClassName"
                 :rowKey="record => record.id"
                 :pagination="pagination"
                 @change="handleTableChange"
                 :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
                 :columns="outTestColumns"
                 :dataSource="data"
        />
      </a-tab-pane>

      <a-tab-pane v-for="pane in panes" :tab="pane" :key="pane">
        <gis :rowData="rowData"></gis>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>

<script>
  import gis from './children/GIS.vue'
  import moment from 'moment'
  import {TestApi} from '@/http/api/test'
  import {downFile} from '@/utils/downFile.js'
  export default {
    name: '',
    components: {
      gis
    },
    data() {
      return {
        rowClassName:(record,index)=>{
          let className = index % 2 ? 'shallow_gray': '';
          return className
        },
        operator:'',
        city:'',
        person:'',
        phone:'',
        outTestColumns:this.$store.state.constant.testColumns,
        data:[],
        selectedRowKeys:[],
        activeKey:'一键测试',
        rangeTime:[],
        panes: [],
        pagination: {
          pageSize:10,
          current:1,
          total:0,
          showTotal: total => `共 ${total} 条数据`,
          showSizeChanger: true,
          pageSizeOptions: ['5', '10', '15', '20'],
        },
        rowData:[]
      }
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    mounted() {

    },
    methods: {
      async query(){
        const params = {
          beginTime: this.rangeTime.length == 0 ? '':moment(this.rangeTime[0]).format('YYYY-MM-DD HH:mm:ss'),
          endTime: this.rangeTime.length == 0 ? '':moment(this.rangeTime[1]).format('YYYY-MM-DD HH:mm:ss'),
          index: 'onebuttontest',
          type: 'onebuttontest',
          page: this.pagination.current,
          limit: this.pagination.pageSize,
          termMap: {
            'operatorService.keyword': this.operator,
            'city.keyword': this.city,
            'testPerson.keyword': this.person
          }
        }
        const data = await TestApi.queryOneButtonTest(params)
        if(data.data.code==0){
          this.data = data.data.data;
          this.pagination.total = data.data.count;
        }
      },
      handleTableChange(pagination){
        this.pagination = pagination;
        this.query();
      },
      onSelectChange(selectedRowKeys,selectedRows){
        this.selectedRowKeys = selectedRowKeys;
        this.rowData = selectedRows;
      },
      //删除
      async deleteTest(){
        if(this.selectedRowKeys.length>0){
          const params = {
            ids:this.selectedRowKeys+"",
            type:'onebuttontest',
            index:'onebuttontest'
          }
          const data = await TestApi.delete(params);
          if(data.success){
            this.query();
          }
        }else{
          this.$message.warning('请先选择要删除的行！');
        }
      },
      //导出
      async exportOneButtonTest(){
        const params={
          ids:this.selectedRowKeys+""
        }
        const data =await TestApi.exportOneButtonTest(params);
        let headers = data.headers;
        let contentType = headers['content-type'];
        if (!data.data) {
          return false;
        } else {
          const blob = new Blob([data.data], {type: contentType});

          const contentDisposition = data.headers['content-disposition'];
          let fileName = 'unknown';
          if (contentDisposition) {
            fileName = window.decodeURI(data.headers['content-disposition'].split('=')[1]);
          }
          downFile(blob, "一键测试报告");
        }
      },

      handleMenuClick(item){
        if(item.key=='GIS'){
          this.add(item.key);
        }else if(item.key=='导出'){
          this.exportOneButtonTest();
        }else{
          this.deleteTest();
        }
      },
      onEdit(targetKey, action) {
        this[action](targetKey)
      },
      add(key){
        if(this.panes.indexOf(key)==-1){
          this.panes.push(key)
        }
        this.activeKey = key;
      },
      remove(targetKey) {
        this.activeKey = '一键测试';
        this.panes=[];
      },


    }
  }
</script>

<style>
  @import "../../../static/css/style.css";

  #outdoorTest .ant-btn-default {
    background: rgb(105, 116, 124);
    color: white;
  }
  #outdoorTest .ant-btn-default:hover {
    background: #586268;
    color: white;
  }
</style>



