<template>
  <div id="mapB">
    <div id="map" :style="{width: '100%',height: '85vh'}">
      <a-spin v-show="ifShowSpin" class="leaflet-loading" size="large"/>
      <div class="leaflet-table" v-show="ifShowTable">
        <a-icon type="close" style="float: right;cursor: pointer;color: red;font-size: 14px" @click="ifShowTable=false" />
        <div>
          <p>运营商：{{this.tableData.operatorService}}</p>
          <p>网络类型：{{this.tableData.netWorkType}}</p>
          <p>城市：{{this.tableData.city}}</p>
          <p>区县：{{this.tableData.county}}</p>
          <p>测试人员姓名：{{this.tableData.testPerson}}</p>
          <p>手机型号：{{this.tableData.phoneType}}</p>
          <p>手机号码：{{this.tableData.phoneNo}}</p>
          <p>IMSI：{{this.tableData.imsi}}</p>
          <p>测试时长：{{this.tableData.testTime}}</p>
          <p>经度：{{this.tableData.longitude}}</p>
          <p>纬度：{{this.tableData.latitude}}</p>
          <p>测试位置：{{this.tableData.testLocation}}</p>
          <p>下载速率(Mbps)：{{this.tableData.downRate}}</p>
          <p>上传速率(Mbps)：{{this.tableData.upRate}}</p>
          <p>ping(ms)：{{this.tableData.pingFlag}}</p>
          <p>http(ms)：{{this.tableData.httpFlag}}</p>
          <p>平均RSRP：{{this.tableData.avgRsrp}}</p>
          <p>平均SINR：{{this.tableData.avgSinr}}</p>
          <p>小区名：{{this.tableData.cellName}}</p>
          <p>eNodeB：{{this.tableData.enodebid}}</p>
          <p>CI：{{this.tableData.ci}}</p>
          <p>频段：{{this.tableData.frequencyBand}}</p>
          <p>PCI：{{this.tableData.pci}}</p>
          <p>TAC：{{this.tableData.tac}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  let map;
  let layers = new L.layerGroup();

  import L from 'leaflet'
  import '../../../../static/js/Leaflet.fullscreen.min'
  import '../../../../static/js/Leaflet.PolylineMeasure'
  import "leaflet/dist/leaflet.css";
  import '../../../../static/css/leaflet.fullscreen.css'
  import '../../../../static/css/Leaflet.PolylineMeasure.css'
  import {mapUtils} from '@/utils/map.js'

  export default {
    components: {

    },
    props: {
     rowData:Array
    },
    name: '',
    data() {
      return {
        map_config: {
          zoom: 13,
          center: [32.03956857467441, 118.63768386683661],
          minZoom: 6,
          maxZoom: 18,
          key: "650887dd02999ce19f403b5a805d2605"
        },
        ifShowSpin:false,
        tableData:{},
        ifShowTable:false,
      };
    },
    beforeCreate() {

    },
    computed: {

    },
    mounted() {
      this.initmap();
      this.initMarkers();
    },
    methods: {
      initmap() {
        map = L.map("map", {
          center: this.map_config.center,
          zoom: this.map_config.zoom,
          fullscreenControl: true,
        });
        mapUtils.addLayer(map,this.map_config,"TianDiTu.Normal.Map");
        mapUtils.addLayer(map,this.map_config,"TianDiTu.Normal.Annotion")
        const options = {
          position: 'topleft',
          measureControlTitleOn: '开启测距',
          measureControlTitleOff: '关闭测距',
          clearControlTitle: '清除测距',
          unit: 'metres',
          clearMeasurementsOnStop: false,
          showClearControl: true,
        }
        L.control.polylineMeasure(options).addTo(map);
        L.control.scale().addTo(map);
      },
      initMarkers(){
        if(this.rowData.length){
          if(layers){
            layers.clearLayers();
          }
          map.setView([this.rowData[0].latitude,this.rowData[0].longitude]);
          this.rowData.map(x=>{
            const marker = mapUtils.createMakerByLatlng(x.latitude,x.longitude).addTo(layers);
            marker.on('click',(e)=>{
              this.tableData = x;
              this.ifShowTable = true;
            })
          })
          layers.addTo(map);
        }else{
          this.$message.warning('没有渲染点信息！');
        }
      }
    }
  }
</script>

<style>
  @import '../../../../static/css/style.css';

  #mapB .ant-table-row td{
    white-space:normal;  /*正常换行*/
  }
  #mapB .ant-card-body{
    padding:10px 5px 10px 10px;
  }

  #modalInput .ant-input {
    margin-bottom: 10px;
  }

  .ant-table-tbody {
    background: white !important;
  }

  .leaflet-loading {
    position: absolute !important;
    z-index: 999 !important;
    right: 50% !important;
    top: 50% !important;
  }
  .leaflet-table{
    background: white;
    min-width:200px;
    padding:10px;
    position: absolute !important;
    z-index: 999 !important;
    right: 2% !important;
    top: 2% !important;
    box-shadow: 10px 10px 5px #888888;
    text-align: left;
  }
  .leaflet-table p{
    margin: 5px;
    font-size: 14px;
  }
</style>



