<template>
  <div id="messageInfo">
    <a-modal title="Message Info" v-model="visible" :footer="null">
      <div v-html="content"></div>
    </a-modal>
  </div>
</template>

<script>
  import {OutdoorTestApi} from '@/http/api/outdoorTest'
  export default {
    name: '',
    components: {

    },
    props:{
      record:Object
    },
    data() {
      return {
        visible:false,
        content:''
      }
    },
    beforeCreate() {

    },
    mounted() {

    },
    methods: {
      //获取messageInfo
      async getMessageInfo(){
        this.visible = true;
        const params = {
          type:"logmessage",
          index:"logmessage",
          limit:1,
          termMap:{
            "id.keyword":this.record.mMeaasge
          },
          revelFields:["message","longitude","latitude"],//固定
          revelPk:"false"
        }
        const data = await OutdoorTestApi.queryList(params);
        this.content = "<pre>"+data.data[0].message+"</pre>"
      }
    }
  }
</script>

<style>
  @import "../../../../../static/css/style.css";
</style>



