import request from '../request' // 引入axios的封装方法
import base from '../base'
import qs from 'qs'

export const TestApi = {
  queryOneButtonTest: (params) => {
    return request('post', `${base.sq}/es/buss/queryOneButtonTest`, params,{})
  },
  delete: (params) => {
    return request('post', `${base.sq}/es/buss/delete`, qs.stringify(params),{})
  },
  exportOneButtonTest:(params) => {
    return request('post', `${base.sq}/es/buss/exportOneButtonTest`, qs.stringify(params),{responseType:'blob'})
  },
}



