import request from '../request' // 引入axios的封装方法
import base from '../base'
import qs from 'qs'

export const OutdoorTestApi = {
  queryOutHome: (params) => {
    return request('post', `${base.sq}/es/buss/queryOutHome`, params,{})
  },
  deleteOutHome:(params) => {
    return request('post', `${base.sq}/es/buss/deleteOutHome`, qs.stringify(params),{})
  },
  exportOutHomeTest:(params) => {
    return request('post', `${base.sq}/es/buss/exportOutHomeTest`, qs.stringify(params),{responseType:'blob'})
  },
  //map页面相关接口
  queryMainLog:(params) => {
    return request('post', `${base.sq}/es/buss/queryMainLog`,params,{})
  },
  //五、根据经纬度查询左下角信息数据
  queryList:(params) => {
    return request('post', `${base.sq}/es/queryList`,params,{})
  },
  //三、根据经纬度查询主服务小区连线
  queryMainService:(params) => {
    return request('post', `${base.sq}/es/buss/queryMainService`,qs.stringify(params),{})
  },
  //四、根据经纬度、PCI查询邻区连线
  queryNearPoint:(params) => {
    return request('post', `${base.sq}/es/buss/queryNearPoint`,params,{})
  },
  //七、SIM工参信息查询 涉及到 经纬度、方位角（地理空间检索）
  boundingBoxQuery:(params) => {
    return request('post', `${base.sq}/es/buss/boundingBoxQuery`,params,{})
  },
  //八、SIM工参信息查询全部(这个可能存在慢的问题，地市数量万级别，省级别十万级别)
  // scrollQuerySimGc:(params) => {
  //   return request('post', `${base.sq}/es/buss/scrollQuerySimGc`,params,{})
  // },
  distinctGcLonLat:(params) => {
    return request('post', `${base.sq}/es/buss/distinctGcLonLat`,params,{})
  },
  //保存图例的接口
  saveLegend:(params) =>{
    return request('post', `${base.sq}/buss/testConfig/edit`,params,{})
  },
  queryLegend:(params)=>{
    return request('post', `${base.sq}/buss/testConfig/query`,qs.stringify(params),{})
  }
}



